<template>

  <div class="container-mini">
    <common-list
      ref="dida_list"
      class="common-list"
      :post-url="list.postUrl"
      :req-data="list.reqData"
      :data-name="list.dataName"
      @returnData="getPrizeLog($event)"
    >

      <div class="content">
        <div class="common-main box">
          <div class="common-list">

            <!-- <div :class="{'focus':ind === 1}" @click="changeBgc(1)" class="list"> -->
            <div
              v-for="(item, index) in list.items"
              :key="index"
              :class="{ focus: ind === index }"
              class="list"
              @click="itemClick(item)"
            >
              <h3><div class="tit"> {{ item.xmmc }} </div> <span class="label">{{ CGFS[item.cgfs] }}</span></h3>
              <div class="info">
                <span>
                  <i class="iconfont iconlianxiren" />
                  {{ item.lxr }}
                </span>
                <span>
                  <i class="iconfont iconshouji" />
                  {{ item.sj }}
                </span>
              </div>
              <div class="info">
                <span>
                  <i class="iconfont iconshijian" />
                  <span v-if="item.kbsj">{{ item.kbsj | formatTime }}</span>
                  <span v-else>-</span>
                </span>
              </div>
            </div>

          </div>
        </div>
      </div>
    </common-list>
  </div>

</template>

<script land="script" scoped>
import constants from '@/common/js/constants'
import commonList from '@/components/list/commonList.vue'
import { mapState, mapActions } from 'vuex'
export default {
  name: 'One',
  components: {
    commonList
  },
  data() {
    return {
      ind: '', // 点击事件
      pageStyles: 0, // 页面状态展示
      pageText: '', // 页面提示信息
      nomore: false, // 没有更多数据
      list: {
        items: [],
        error: false, // 是否在刷新
        postUrl: '',
        reqData: '',
        dataName: '',
        noDataText: ''
      }
    }
  },
  computed: {
    // 取数据
    ...mapState({
      isLogin: state => state.mh.mhisLogin,
      userInfo: state => state.mh.mhUserInfo
    })
  },
  created() {
    this.ind = ''
    this.XMZT = constants.XMZT
    this.CGFS = constants.CGFS
    // if (this.isLogin) {
    // this.refreshData()
    this.getWdxmLists()
    this.$bus.$on('searchChildJxz', (msg) => { // bus接收事件
      console.log('msg==============', msg)
      this.$refs.dida_list.onRefresh(msg)
    })
    // this.pageStyle = true
    // } else {
    // Toast.failed('暂未登录！')
    // this.pageStyles = -1
    // }
  },
  beforeDestroy() {
    this.$bus.$off('searchChildJxz')
  },
  methods: {
    ...mapActions([
      'getWdxmList'
    ]),
    changeBgc: function(index) {
      this.ind = index
    },
    // parentReceive() {
    //   this.getWdxmLists()
    // },
    getPrizeLog(data) {
      this.list.items = data
    },
    getWdxmLists() {
      this.list.postUrl = 'gyswdxm/getWdxmListAction'
      this.list.reqData = {
        pageno: this.pageno,
        zzid: this.userInfo.zzid,
        // xmmc: '',
        gyscyxmzt: '2'
      }
      this.list.dataName = 'xmxxList'
    },

    /* 获取最新数据*/
    getNewData() {
      this.getWdxmLists()
    },
    scrollToEnd() {
      this.$refs.scrollContent.shanglajiazai()
    },

    refreshTip(pos) {
      if (pos.y > 20) {
        this.$refs.scrollContent.refreshTip()
      }
    },
    itemClick(item) {
      this.$store.commit('gyswybm/SET_WYBM_LIST_ITEM', item)
      this.$store.commit('gyswdxm/SET_XMID', item.xmid)
      this.$store.commit('gyswdxm/SET_BMGYSID', item.bmgysid)
      this.$store.commit('gyswdxm/SET_SFDLBB', item.sfdlbb)
      this.$store.commit('gyswdxm/SET_XMMC', item.xmmc)
      if (item.bmgyszt != null && item.bmgyszt >= 2) {
        this.$router.push({ name: 'Wdxmxmxq' })
      } else {
        this.$store.dispatch('gyswdxm/getStartGyslcAction', { id: item.bmgysid, bmrbh: this.userInfo.id }).then(res => {
          this.$router.push({ name: 'Wdxmxmxq' })
        })
      }
    },
    /* 加载更多数据*/
    getMoreData() {
      if (this.pageno === this.totalpageno) {
        this.nomore = true
      } else {
        this.pageno = this.pageno + 1
        this.getWdxmLists()
      }
    }

  }

}
</script>

<style lang="css" scoped>

</style>
