<template>
  <div class="container-mini">
    <common-list
      ref="dida_list"
      class="common-list"
      :post-url="list.postUrl"
      :req-data="list.reqData"
      :data-name="list.dataName"
      @returnData="getPrizeLog($event)"
    >
      <div class="content">
        <div class="common-main box">
          <div :class="{'focus':ind === 1}">

            <div
              v-for="(item, index) in list.items"
              :key="index"
              class="list"
              :class="{ focus: ind === index }"
              @click="itemClick(item)"
            >

              <h3><div class="tit"> {{ item.xmmc }} </div> <span class="label">{{ CGFS[item.cgfs] }}</span></h3>
              <div class="info">
                <!--  <span class="label">{{ CGFS[item.cgfs] }}</span> -->
                <span>
                  <i class="iconfont iconlianxiren" />
                  {{ item.lxr }}
                </span>
                <span>
                  <i class="iconfont iconshouji" />
                  {{ item.sj }}
                </span>
              </div>
              <div class="info">
                <span>
                  <i class="iconfont iconshijian" />
                  <span v-if="item.kbsj">{{ item.kbsj | formatTime }}</span>
                  <span v-else>-</span>
                </span>
              </div>

              <!-- <div class="list-btn" @click="goWybj(item)">
                <ul>
                  <li><van-button type="default" plain size="small"><i class="iconfont iconxiugai" /> 我要报价</van-button></li>
                </ul>
              </div> -->
            </div>

          </div>
        </div>
      </div>
    </common-list>
  </div>
</template>

<script land="script" scoped>
// import xyScroll from '@/components/scroll/xyScroll.vue' // 导航栏
import constants from '@/common/js/constants'
// import errorPage from '@/components/result-page/index'
import { mapState, mapActions } from 'vuex'
import commonList from '@/components/list/commonList.vue'

export default {
  name: 'One',
  components: {
    commonList
    // xyScroll,
    // errorPage
  },
  data() {
    return {
      ind: '', // 点击事件
      pageStyles: 0, // 页面状态展示
      allList: [], // 数据展示列表
      pageno: 1, // 页
      totalpageno: 0, // 总页数
      nomore: false, // 没有更多数据
      list: {
        items: [],
        error: false, // 是否在刷新
        postUrl: '',
        reqData: '',
        dataName: '',
        noDataText: ''
      }
    }
  },
  // computed() {
  //   this.ind = ''
  // },
  computed: {
    // 取数据
    ...mapState({
      isLogin: state => state.mh.mhisLogin,
      userInfo: state => state.mh.mhUserInfo
    })
  },
  created() {
    this.ind = ''
    this.XMZT = constants.XMZT
    this.CGFS = constants.CGFS
    this.getWdxmLists()
    // 不会引起DOM变化的数据在此定义
    this.$bus.$on('searchChildAll', (msg) => { // bus接收事件
      console.log('msg==============', msg)
      this.$refs.dida_list.onRefresh(msg)
    })
    // if (this.isLogin) {
    //   // this.refreshData()
    //   this.getWdxmLists()
    //   // this.pageStyle = true
    // } else {
    //   this.$toast.fail('暂未登录！')
    //   this.pageStyles = -1
    // }
  },
  beforeDestroy() {
    this.$bus.$off('searchChildAll')
  },

  mounted() {
    // window.ScrollViewTrigger1 = () => {
    //   this.$refs.scrollView.triggerRefresh()
    // }
  },
  methods: {
    ...mapActions([
      'getWdxmList'
    ]),
    changeBgc: function(index) {
      this.ind = index
    },

    getPrizeLog(data) {
      this.list.items = data
    },
    getWdxmLists() {
      this.list.postUrl = 'gyswdxm/getWdxmListAction'
      this.list.reqData = {
        pageno: this.pageno,
        zzid: this.userInfo.zzid,
        // xmmc: '',
        gyscyxmzt: ''
      }
      this.list.dataName = 'xmxxList'
    },
    goWybj(item) {
      console.info('xmid====', item, '====', item.xmid)
      this.$store.commit('gyswybm/SET_WYBM_LIST_ITEM', item)
      this.$store.commit('gyswdxm/SET_XMID', item.xmid)
      this.$store.commit('gyswdxm/SET_BMGYSID', item.bmgysid)
      this.$store.commit('gyswdxm/SET_SFDLBB', item.sfdlbb)
      this.$store.commit('gyswdxm/SET_XMMC', item.xmmc)
      this.$router.push({ name: 'WdxmBj', params: { 'xmid': item.xmid }})
    },
    itemClick(item) {
      this.$store.commit('gyswybm/SET_WYBM_LIST_ITEM', item)
      this.$store.commit('gyswdxm/SET_XMID', item.xmid)
      this.$store.commit('gyswdxm/SET_BMGYSID', item.bmgysid)
      this.$store.commit('gyswdxm/SET_SFDLBB', item.sfdlbb)
      this.$store.commit('gyswdxm/SET_XMMC', item.xmmc)
      if (item.bmgyszt != null && item.bmgyszt >= 2) {
        this.$router.push({ name: 'Wdxmxmxq' })
      } else {
        this.$store.dispatch('gyswdxm/getStartGyslcAction', { id: item.bmgysid, bmrbh: this.userInfo.id }).then(res => {
          this.$router.push({ name: 'Wdxmxmxq' })
        })
      }
    }

  }

}
</script>

<style lang="css" scoped>

</style>
