<template>
  <div>
    <headerBar :header-data="headerData" @searchClick="searchAction()" />
    <div ref="testref" class="container-tab">
      <van-sticky class="tabs-sticky">
        <!-- 主体信息 begin -->
        <van-tabs :active="active" swipeable animated sticky @click="onClick">
          <!-- <van-tab v-for="index in tabs" :key="index" :title=" index">
           <jbr-db-all ref="mychild" />
        </van-tab> -->
          <van-tab v-for="(item ,index) in tabs" :key="index" :title="item">
            <all v-if="index == 0 " ref="mychild" />
            <jxz v-if="index == 1 " ref="mychild" />
            <ywc v-if="index == 2 " ref="mychild" />
          </van-tab>
        </van-tabs>
        <!-- 主体信息 end -->
      </van-sticky></div>

    <FooterNav />

    <searchMeun @goSearchAction="searchXmxx($event)" @reSet="reSet">
      <van-field v-model="search.xmmc" label="项目名称" placeholder="请输入项目名称" input-align="right" clearable />
      <van-field v-model="search.xmbh" label="项目编号" placeholder="请输入项目编号" input-align="right" clearable />
      <van-cell v-model="showCgfs" title="采购方式" is-link @click="showcgfsPopup()" />
      <van-cell v-model="date" title="开标时间" is-link @click="showCalendars()" />
    </searchMeun>
    <searchPick
      :pick-title="searchPick.PickTitle"
      :pick-columns="searchPick.PickColumns"
      @confirmClick="confirmPick($event)"
    />
    <searchCalendar
      :calendar-name="searchCalendar.calendarName"
      @confirmCalendar="confirmCalendar($event)"
    />

  </div>
</template>

<script>
import FooterNav from '@/components/footerNav/footerNav.vue'
// import SearchBox from '@/components/search/search-box'
import all from './project/all'
import jxz from './project/jxz'
import ywc from './project/ywc'
import { pageRouter } from '@/common/js/mixin.js'
import headerBar from '@/components/header/headerBar.vue'
import searchMeun from '@/components/searchComponents/searchMeun.vue'
import searchPick from '@/components/searchComponents/searchPick.vue'
import searchCalendar from '@/components/searchComponents/searchCalendar.vue'
import constants from '../../../common/js/constants'

import {
  mapState,
  mapActions
} from 'vuex'
  // import { getWdxmListAction } from '../../store/module/wdxm'
export default {
  name: 'Wdxm',
  components: {
    FooterNav,
    searchMeun,
    searchPick,
    searchCalendar,
    headerBar,
    all,
    jxz,
    ywc
  },
  mixins: [pageRouter],
  data() {
    return {
      // slideIndex: 0, // 点击导航发送给swiper的值
      // swiperToNav: 0 // swiper滑动下标值传给nav导航
      active: 0,
      headerData: {
        title: '我参与的项目',
        leftUrl: '/mhhome',
        leftText: ' ',
        showSearch: true
      },
      tabs: ['全部', '未完成', '已完成'],
      search: {
        xmmc: '',
        xmbh: '',
        cgfs: '',
        kssj: '',
        jssj: ''
      },
      query: '', // 搜索
      searchPick: {
        PickTitle: '',
        PickColumns: ''
      },
      searchCalendar: {
        calendarName: ''
      },
      date: '请选择开标-结束时间',
      xmmc: '',
      pageName: '',
      xmbh: '',
      cgfs: '', // 获取poup组件的值的方法自己百度
      showCgfs: '请选择采购方式',
      kbsjbegin: '', // 获取时间组件的值的方法自己百度
      kbsjend: '',
      show: false,
      show1: false,
      show2: false,
      minDate: new Date(1999, 0, 1),
      maxDate: new Date(2099, 0, 1),
      defaultDate: [new Date(), new Date(new Date().setDate(new Date().getDate() + 1))]
    }
  },
  computed: {
    ...mapState({
      userInfo: state => state.mh.mhUserInfo,
      ygfbList: state => state.jbrwdxm.ygfbList,
      wgfbList: state => state.jbrwdxm.wgfbList
    })
  },
  created() {
    this.XMZT = constants.XMZT
    this.CGFS = constants.CGFS
    this.getCGFS()
  },
  // watch: {
  //   // 监听导航传给swiper时候，把数据传给父亲时候传参值的变化
  //   slideIndex(value) {
  //     // swiper滑动下标值传给nav导航,目的是为了导航的下标和swiper的下标索引能时时同步不存在bug
  //     this.swiperToNav = value
  //   },
  //   swiperToNav(value) {
  //     // 点击导航发送给swiper的值,目的是为了导航的下标和swiper的下标索引能时时同步不存在bug
  //     this.slideIndex = value
  //   }
  // },
  methods: {
    reSet() {
      this.search = {
        xmmc: '',
        xmbh: '',
        cgfs: '',
        kssj: '',
        jssj: ''
      }
      this.showCgfs = '请选择采购方式'
      this.date = '请选择开标-结束时间'
    },
    ...mapActions([
      'getWdxmList'
    ]),
    onCgfsCancel() {
      this.show1 = false
    },
    onCgfsConfirm(pick) {
      this.show1 = false
      this.search.cgfs = pick.value
      this.showCgfs = pick.label
    },
    // 搜索数据
    onQueryChange(query) {
      this.query = query
    },
    onClick(data, ref) {
      this.pageName = ref
    },
    getCGFS() {
      this.$store
        .dispatch('gyswybm/getCgfsAction')
        .then(res => {
          const maps = []
          for (var key in res) {
            console.log(key, res[key])
            maps.push({ 'value': key, 'label': res[key] })
          }
          this.searchPick.PickColumns = maps
        })
        .catch((error) => {
          // this.pageStyles = 3
          this.$toast.fail(error)
          // this.loading = false
        })
      // const maps = []
      // for (var key in this.CGFS) {
      //   maps.push({ 'value': key, 'label': this.CGFS[key] })
      // }
      // this.searchPick.PickColumns = maps
      // console.info('maps=====', maps)
    },
    // 展示采购方式
    showcgfsPopup() {
      // this.searchPick.PickShow = true
      this.$bus.$emit('showPick', true)
      this.searchPick.PickTitle = '选择采购方式'
    },
    // 日历回调
    confirmCalendar(date) {
      const [start, end] = date
      this.show2 = false
      this.kbsjbegin = this.formatDate8(start)
      this.kbsjend = this.formatDate8(end)
      this.date = `${this.formatDateShow(start)}-${this.formatDateShow(end)}`
      this.search.kssj = parseInt(this.kbsjbegin)
      this.search.jssj = parseInt(this.kbsjend)
    },
    searchXmxx() {
      this.$bus.$emit('showMeun', false)
      const query = { xmmc: this.search.xmmc, xmbh: this.search.xmbh, cgfs: this.search.cgfs, kssj: this.search.kssj, jssj: this.search.jssj }
      const child = this.pageName
      console.log('chchchchchch', child)
      if (child === '未完成') {
        this.$bus.$emit('searchChildJxz', query) // bus触发事件
      } else if (child === '已完成') {
        this.$bus.$emit('searchChildYwc', query) // bus触发事件
      } else {
        this.$bus.$emit('searchChildAll', query) // bus触发事件
      }
      this.show = false
      // if (this.query !== '' && this.query !== null) {
      //   this.$refs.mychild.parentHandleclick(this.query)
      // } else {
      //   this.$toast.fail('请输入搜索条件')
      // }
    },
    backRoute() {
      this.$router.go(-1)
    },
    // 采购方式回调
    confirmPick(pick) {
      this.showCgfs = pick.label
      this.search.cgfs = pick.value
      console.log('pick==========', pick.value)
      console.log('pick==========', pick.label)
    },
    // 展示日历
    showCalendars() {
      console.log('showCalendars==========')
      this.$bus.$emit('showCalendar', true)
      this.searchCalendar.calendarName = '请选择开标-结束时间'
      // this.searchCalendar.showCalendar = true
    },
    // 点击导航菜单通过emit传给父亲然后父亲再传给子组件swiper组件
    clickIndex(index) {
      this.slideIndex = index
      // this.$store.commit('saveNavIndex', index)
    },

    goSearchQr() {
      if (this.query !== '' && this.query !== null) {
        this.$refs.mychild.parentHandleclick(this.query)
      } else {
        this.$toast.fail('请输入搜索条件')
      }
    },

    // swiper滑动后下标通过emit传给父亲。父亲再传给子组件nav
    slideSendNav(index) {
      this.swiperToNav = index
      // this.$store.commit('saveSwiperIndex', index)
    },
    formatDate8(date) {
      var month = date.getMonth() + 1
      var day = date.getDate()
      if (month < 10) {
        month = '0' + month
      }
      if (day < 10) {
        day = '0' + day
      }
      return `${date.getFullYear()}${month}${day}`
    },
    formatDateShow(date) {
      return `${date.getFullYear()}/${date.getMonth() + 1}/${date.getDate()}`
    }
  }

}
</script>

<style lang="css" scoped>
.nav-bar{box-shadow: none;}
</style>
